<template>
  <div id="giftDetail">
    <common-header :title="'礼物详情'"></common-header>
    <div class="giftDetail__info">
      <div class="info__user">
        <div class="user__avatar">
          <img src="../../../assets/avatar.png" alt="">
        </div>

        <div class="user__right">
          <div class="user__right__info">
            <div class="info__name">
              <span>李婷婷</span>
              <span class="female"></span>
            </div>
            <div class="info__date">2017-10-17 17:45:10</div>
          </div>

          <div class="user__right__btn">
            <div class="btn__icon"></div>
            <div class="btn__text">回礼</div>
          </div>
        </div>
      </div>
      <div class="info__message">
        <div class="info__message__content">少时诵诗书所所所所所所所所所</div>
        <div class="info__message__video">
          <img class="video" src="../../../assets/pic.png" alt="">
          <div class="play">
            <div class="play__icon"></div>
            <div class="play__text">祝福视频</div>
          </div>
        </div>
      </div>
    </div>

    <Gift class="giftDetail__good" />

    <div class="giftDetail__btn">
      <div class="btn" @click="transmit">转赠</div>
      <div class="btn" @click="linkTo('Recover')" v-if="type == 'normal'">回收</div>
      <div class="btn col" @click="linkTo('Send')">
        <div class="send">实物发货</div>
        <div class="time">(72小时22分)</div>
      </div>
    </div>

    <!-- 转赠按钮 -->
    <van-actionsheet v-model="showSend" :actions="actions" :cancel-text="'取消'"/>
  </div>
</template>

<script>
  import Gift from '@/components/Goods/Goods';
  export default {
    data() {
      return {
        showSend: false, //是否显示actionsheet
        actions: [
          {
            name: '微信好友',
            callback: () => {
              console.log(1)
              this.linkToUrl('transmit');
            }
          },
          {
            name: '我的好友',
            callback: () => {
              console.log(2)
              this.linkToUrl('transmit');
            }
          }
        ],
        type: this.$route.query.type
      }
    },

    methods: {
      //转赠礼物
      transmit() {
        this.showSend = true;
      }
    },

    components: {
      Gift
    }
  }
</script>


<style lang="scss">
  #giftDetail {
    height: 100%;
    background: #f0f0f0;

    .giftDetail__info {
      background: #fff;

      .info__user {
        display: flex;
        flex-direction: row;
        padding: 0.267rem 0.4rem;
        height: 1.6rem;
        box-sizing: content-box;

        .user__avatar {
          width: 2rem;
          margin-right: 0.4rem;

          img {
            width: 1.6rem;
            height: 1.6rem;
            border-radius: 50%;
          }
        }

        .user__right {
          display: flex;
          width: 100%;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;

          .user__right__info {
            display: flex;
            height: 100%;
            flex-direction: column;
            justify-content: space-around;

            .info__name {
              font-size: 0.4rem;
              color: #333;
            }

            .info__date {
              color: #666;
            }
          }

          .user__right__btn {
            .btn__icon {
              margin: 0.1rem auto;
              width: 0.48rem;
              height: 0.48rem;
              background: url('../../../assets/gift_back.png') no-repeat;
              background-size: 100%;
            }
            
            .btn__text {
              color: #666;
              font-size: 0.267rem;
            }
          }
        }
      }

      .info__message {
        padding: 0 0.4rem;
        padding-bottom: 1px; 

        .info__message__content {
          font-size: 0.4rem;
        }

        .info__message__video {
          position: relative;
          margin: 0.4rem 0;
          width: 2.8rem;

          .video {
            width: 2.8rem;
            height: 2.8rem;
          }

          .play {
            position: absolute;
            left: 0;
            top: 0;
            width: 2.8rem;
            height: 2.8rem;
            z-index: 99;

            .play__icon {
              position: absolute;
              left: 50%;
              top: 50%;
              transform: translate(-50%, -50%);
              width: 0.587rem;
              height: 0.587rem;
              background: url('../../../assets/btn_play.png') no-repeat;
              background-size: 100%;
            }

            .play__text {
              position: absolute;
              left: 50%;
              bottom: 0.3rem;
              width: 100%;
              transform: translateX(-50%);
              font-size: 0.32rem;
              text-align: center;
              color: #fff;
            }
          }

          &::after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0, 0.4);
          }
        }
      }
    }

   

    .giftDetail__btn {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      width: 100%;
      height: 1.31rem;
      background: #fff;
      font-size: 0;

      .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        flex:1;
        height: 100%;
        font-size: 0.453rem;
        text-align: center;
        color: #de7fef;
      }

      .btn+.btn {
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 15%;
          height: 70%;
          left: 0;
          border-left: 1px solid #eee;
        }
      }

      .col {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
      }
    }
  }
</style>



